const ContactPanel = () => {
  const [contactData, setContactData] = useState<ContactContent>();

  useEffect(() => {
    axios.get("/config/Contact.json").then((data) => {
      let contactDataParse: ContactContent = JSON.parse(
        JSON.stringify(data.data)
      );
      setContactData(contactDataParse);
    });
  }, []);

  return (
    <div className="flex flex-col gap-5 items-center mb-20">
      <div className="text-4xl font-bold"></div>
      <Contact
        email={contactData?.email}
        entry={contactData?.entry}
        description={contactData?.description}
        instagram={contactData?.instagramURL}
        line={contactData?.lineURL}
      />
    </div>
  );
};

const ActivityPanel = () => {
  let [activityData, setActivityData] = useState<ActivityContent[]>();

  useEffect(() => {
    axios.get("/config/Activity.json").then((data) => {
      let activityParse: Array<ActivityContent> = JSON.parse(
        JSON.stringify(data.data)
      );
      setActivityData(activityParse.map((data) => data));
    });
  }, []);

  return (
    <div className="flex flex-col gap-40">
      {activityData?.map((data, index) => {
        return (
          <Activity
            title={data.title}
            description={data.description}
            image={data.image}
            activity={data.activity}
            reverseView={Boolean(index % 2)}
          />
        );
      })}
    </div>
  );
};

const FAQPanel = () => {
  const [faqData, setFAQData] = useState<FAQContent[]>();
  useEffect(() => {
    axios.get("/config/FAQ.json").then((data) => {
      let faqListParse: Array<FAQContent> = JSON.parse(
        JSON.stringify(data.data)
      );
      setFAQData(faqListParse.map((data) => data));
    });
  }, []);

  return (
    <div className="flex flex-col gap-8 items-center">
      <div className="text-4xl font-bold"></div>
      <div className="flex flex-col gap-3">
        {faqData?.map((faq) => {
          return <FAQ title={faq.title} content={faq.content} />;
        })}
      </div>
    </div>
  );
};

const MemberPanel = (props: {
  memberImageURL: string | undefined;
  mainGroupURL: string | undefined;
}) => {
  const memberImageAnimation = useAnimation();
  const titleAnimation = useAnimation();
  const descriptionAnimation = useAnimation();
  const linkButtonAnimation = useAnimation();

  return (
    <motion.div
      viewport={{ amount: 1 }}
      onViewportEnter={() => {
        memberImageAnimation
          .start({
            height: 450,
            transition: { duration: 0.8, ease: "circOut" },
          })
          .then(() => {
            titleAnimation.start({ opacity: 1 }).then(() => {
              descriptionAnimation.start({ opacity: 1 }).then(() => {
                linkButtonAnimation.start({ opacity: 1 });
              });
            });
          });
      }}
      className="flex flex-col items-center gap-5"
    >
      <div className="text-4xl font-bold"></div>
      <motion.img
        animate={memberImageAnimation}
        initial={{ height: 200 }}
        className="flex rounded-3xl shadow-roundXL object-center"
        src={props.memberImageURL}
      />
      <div className="flex flex-col items-center gap-2">
        <motion.div
          className="text-2xl font-bold"
          animate={titleAnimation}
          initial={{ opacity: 0 }}
        >
          
        </motion.div>
        <motion.div
          className="flex flex-col items-center text-sm"
          animate={descriptionAnimation}
          initial={{ opacity: 0 }}
        >
          <div>
            Community
            Lab
          </div>
          <div>
            
          </div>
        </motion.div>
      </div>
      <Link to={props.mainGroupURL || ""} target="_blank">
        <motion.div
          animate={linkButtonAnimation}
          initial={{ opacity: 0 }}
          className={`p-3 px-4 text-xs rounded-3xl bg-slate-500 text-white select-none cursor-pointer ${
            props.mainGroupURL === "" ? "collapse" : ""
          }`}
        >
          HP
        </motion.div>
      </Link>
    </motion.div>
  );
};

function Home() {
  const [config, setConfigData] = useState<ConfigContent>();

  useEffect(() => {
    axios.get("/config/Config.json").then((data) => {
      let memberParse: ConfigContent = JSON.parse(JSON.stringify(data.data));
      setConfigData(memberParse);
    });
  }, []);

  return (
    <div className="flex flex-col items-center gap-48">
      <IntroducePanel />
      <CharacterCardPanel />
      <ActivityPanel />
      <MemberPanel
        mainGroupURL={config?.mainGroupURL}
        memberImageURL={config?.memberImageURL}
      />
      <FAQPanel />
      <ContactPanel />
    </div>
  );
}

export default Home;
